.BuildControls {
    width: 100%;
    background-color: #343334;
    color: FFF;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-align: center;
        align-items: center;
    -webkit-box-shadow: 0 2px 1px #ccc;
            box-shadow: 0 2px 1px #ccc;
    margin: auto;
    padding: 10px 0;
}


.OrderButton {
    display: block;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    width: 200px;
    font: inherit;
    border: 0px;
    border-radius: 0.1em;
    padding: 16px 28px 16px 28px;
    cursor: pointer;
    margin: 3.5px;
    font-size: 20px;

    background-color: teal;
    color: #FFF;
    
    padding: 15px 30px;
    -webkit-box-shadow: 2px 2px 2px orange;
            box-shadow: 2px 2px 2px orange;
}

.OrderButton:hover, .OrderButton:active {
    background-color: #5BB76E;
    color: rgb(0, 7, 2);
}

.OrderButton:disabled {
    -webkit-box-shadow: none;
            box-shadow: none;
    background-color: #C7C6C6;
    cursor: not-allowed;
    border: 1px solid #ccc;
    color: #888888;
}

.OrderButton:not(:disabled) {
    -webkit-animation: enable 0.3s linear;
            animation: enable 0.3s linear;
}

@-webkit-keyframes enable {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes enable {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}


