.UserInput {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    width: 200px;
    font: inherit;
    border: 0px;
    border-radius: 0.1em;
    padding: 16px 28px 16px 28px;
    cursor: pointer;
    margin: 3.5px;
    font-size: 20px;
    text-align: center;
}

.QuantityBtn {
    /* display: inline; */
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    width: 200px;
    font: inherit;
    border: 0px;
    border-radius: 0.1em;
    padding: 16px 28px 16px 28px;
    cursor: pointer;
    margin: 3.5px;
    font-size: 20px;
    text-align: center;
}

.ConfirmBtn {
    display: block;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    width: 200px;
    font: inherit;
    border: 0px;
    border-radius: 0.1em;
    padding: 16px 28px 16px 28px;
    cursor: pointer;
    margin: 3.5px;
    font-size: 20px;
}

@media (max-width: 500px) {
    .BuildControl {
        display: block !important;
        text-align: center !important;
    }
}


